import React, { useState, useEffect, useRef } from 'react';
import cardbg1 from '../assets/cardbg1.png';
import cardbg2 from '../assets/cardbg22.png';
import cardbg3 from '../assets/cardbg3.png';
import cardbg4 from '../assets/cardbg4.png';

const Card = ({ cardbg, header, content, link }) => {
  const cardRef = useRef(null);
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);
  const [mouseX, setMouseX] = useState(0);
  const [mouseY, setMouseY] = useState(0);
  const [mouseLeaveDelay, setMouseLeaveDelay] = useState(null);

  useEffect(() => {
    const updateDimensions = () => {
      if (cardRef.current) {
        setWidth(cardRef.current.offsetWidth);
        setHeight(cardRef.current.offsetHeight);
      }
    };

    updateDimensions();
    window.addEventListener('resize', updateDimensions);

    return () => {
      window.removeEventListener('resize', updateDimensions);
      if (mouseLeaveDelay) {
        clearTimeout(mouseLeaveDelay);
      }
    };
  }, [cardRef, mouseLeaveDelay]);

  const handleMouseMove = throttle((e) => {
    if (cardRef.current) {
      const rect = cardRef.current.getBoundingClientRect();
      setMouseX(e.clientX - rect.left - width / 2);
      setMouseY(e.clientY - rect.top - height / 2);
    }
  }, 100); // 限制每 100 毫秒调用一次

  const handleMouseEnter = () => {
    if (mouseLeaveDelay) {
      clearTimeout(mouseLeaveDelay);
    }
  };

  const handleMouseLeave = () => {
    setMouseLeaveDelay(setTimeout(() => {
      setMouseX(0);
      setMouseY(0);
    }, 1000));
  };

  const mousePX = mouseX / width;
  const mousePY = mouseY / height;

  const cardStyle = {
    transform: `rotateY(${mousePX * 30}deg) rotateX(${mousePY * -30}deg)`
  };

  const cardBgTransform = {
    transform: `translateX(${mousePX * -40}px) translateY(${mousePY * -40}px)`
  };

  const cardBgImage = {
    backgroundImage: `url(${cardbg})`
  };

  return (
    <div
      className="card-wrap"
      onMouseMove={handleMouseMove}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      ref={cardRef}
    >
      <div className="card" style={cardStyle} onClick={() => window.open(link)}>
        <div className="card-bg" style={{ ...cardBgTransform, ...cardBgImage }}></div>
        <div className="card-info">
          <h1>{header}</h1>
          <p>{content}</p>
        </div>
      </div>
    </div>
  );
};

const HoverCard = () => {
  return (
    <>
      <div className="container">
        <Card
          cardbg={cardbg1}
          header="HUAWEI"
          content="Lorem ipsum dolor sit amet, consectetur adipisicing elit."
          link='https://www.huawei.com/cn/'
        />
        <Card
          cardbg={cardbg2}
          header="ByteDance"
          content="Lorem ipsum dolor sit amet, consectetur adipisicing elit."
          link='https://www.bytedance.com/zh/'
        />
        <Card
          cardbg={cardbg3}
          header="Xiaomi"
          content="Lorem ipsum dolor sit amet, consectetur adipisicing elit."
          link='https://www.mi.com/'
        />
        <Card
          cardbg={cardbg4}
          header="Baidu"
          content="Lorem ipsum dolor sit amet, consectetur adipisicing elit."
          link='https://www.baidu.com/'
        />
      </div>
    </>
  );
};

export default HoverCard;

// 声明 throttle 函数
function throttle(fn, delay) {
  let lastCall = 0;
  return function(...args) {
    const now = Date.now();
    if (now - lastCall >= delay) {
      lastCall = now;
      return fn.apply(this, args);
    }
  };
}