import { useState } from 'react';
import { Form, Input, Button, Checkbox, Flex, notification } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';

// import './entry-page.css'

const EntryPage = () => {
  const nav = useNavigate();
  const [api, contextHolder] = notification.useNotification();
  const [loginForm] = Form.useForm()
  const [singUpForm] = Form.useForm()
  const [PWResetForm] = Form.useForm()


  const [view, setView] = useState('login')
  const changeView = (view) => {
    loginForm && loginForm?.resetFields()
    singUpForm && singUpForm?.resetFields()
    PWResetForm && PWResetForm?.resetFields()
    setView(view)
  }

  const onFinishLogin = (values) => {
    console.log('登录表单提交成功:', values);
    loginForm.resetFields()
    nav('/home')
  };
  const onFinishRegister = (values) => {
    console.log('注册表单提交成功:', values);
    api.success({
      message: '注册成功！',
      duration: 1.5
    })
    changeView('login')
  };
  const onFinishResetPW = (values) => {
    console.log('重置密码成功', values)
    api.success({
      message: '重置成功，请重新登录！',
      duration: 1.5
    })
    changeView('login')
  }

  const currentView = () => {
    switch (view) {
      case "signUp":
        return (
          <Form
            name="signUp"
            form={singUpForm}
            onFinish={onFinishRegister}
            variant='borderless'
          >
            <div className="login-title">注册</div>
            <Form.Item
              name="username"
              className='register-input'
              rules={[{ required: true, message: '请输入用户名', whitespace: true }]}
            >
              <Input placeholder='用户名' />
            </Form.Item>

            <Form.Item
              name="password"
              className='register-input'
              rules={[
                {
                  required: true,
                  message: '请输入密码',
                },
              ]}
              hasFeedback
            >
              <Input.Password placeholder='密码' />
            </Form.Item>

            <Form.Item
              name="confirm"
              className='register-input'
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: '请确认密码',
                },
                ({ getFieldValue }) => ({
                  validator (_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error('两次输入不一致'));
                  },
                }),
              ]}
            >
              <Input.Password placeholder='确认密码' />
            </Form.Item>

            <Form.Item
              name="question"
              className='register-input'
              rules={[{ required: true, message: '请输入密保问题' }]}
            >
              <Input.TextArea style={{ height: 100 }} placeholder="密保问题【请输入你最喜欢的一样东西】" showCount maxLength={100} />
            </Form.Item>

            <Form.Item
              name="agreement"
              valuePropName="checked"
              rules={[
                {
                  validator: (_, value) =>
                    value ? Promise.resolve() : Promise.reject(new Error('请点击同意条款')),
                },
              ]}
            >
              <Checkbox>
                我已阅读 隐私声明
              </Checkbox>
            </Form.Item>
            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' type="primary" htmlType="submit">
                注册
              </Button>
              <span className='register' onClick={() => changeView('login')}>返回</span>
            </Form.Item>
          </Form>
        )
        break
      case "login":
        return (
          <Form
            form={loginForm}
            name="login"
            initialValues={{ remember: true }}
            onFinish={onFinishLogin}
            variant='boderless'
          >
            <div className="login-title">登录</div>
            <Form.Item
              name="username"
              className='username'
              rules={[{ required: true, message: '请输入用户名' }]}
            >
              <Input prefix={<UserOutlined />} placeholder="用户名" />
            </Form.Item>
            <Form.Item
              name="password"
              className='password'
              rules={[{ required: true, message: '请输入密码' }]}
            >
              <Input prefix={<LockOutlined />} type="password" placeholder="密码" />
            </Form.Item>
            <Form.Item>
              <Flex justify="space-between" align="center">
                <Form.Item className='remember' name="remember" valuePropName="checked" noStyle>
                  <Checkbox>记住我</Checkbox>
                </Form.Item>
                <span className='reset-password' onClick={() => changeView('PWReset')}>忘记密码</span>
              </Flex>
            </Form.Item>

            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' block type="primary" htmlType="submit">
                登 录
              </Button>
              <span className='register' onClick={() => changeView('signUp')}>马上注册!</span>
            </Form.Item>
          </Form>
        )
        break
      case "PWReset":
        return (
          <Form
            name="PWReset"
            form={PWResetForm}
            onFinish={onFinishResetPW}
            variant='borderless'
          >
            <div className="login-title">重置密码</div>
            <Form.Item
              name="username"
              className='register-input'
              rules={[{ required: true, message: '请输入用户名', whitespace: true }]}
            >
              <Input placeholder='用户名' />
            </Form.Item>

            <Form.Item
              name="password"
              className='register-input'
              rules={[
                {
                  required: true,
                  message: '请输入新密码',
                },
              ]}
              hasFeedback
            >
              <Input.Password placeholder='新密码' />
            </Form.Item>

            <Form.Item
              name="confirm"
              className='register-input'
              dependencies={['password']}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: '请确认新密码',
                },
                ({ getFieldValue }) => ({
                  validator (_, value) {
                    if (!value || getFieldValue('password') === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(new Error('两次输入不一致'));
                  },
                }),
              ]}
            >
              <Input.Password placeholder='确认新密码' />
            </Form.Item>

            <Form.Item
              name="question"
              className='register-input'
              rules={[{ required: true, message: '请回答注册时密保问题' }]}
            >
              <Input.TextArea style={{ height: 100 }} placeholder="密保问题【请回答你最喜欢的一样东西】" showCount maxLength={100} />
            </Form.Item>

            <Form.Item className='submit'>
              <Button className='login-form-button' size='large' type="primary" htmlType="submit">
                提交
              </Button>
              <span className='register' onClick={() => changeView('login')}>返回</span>
            </Form.Item>
          </Form>
        )
      default:
        break
    }
  }

  return <section className='entry-page'>
    <div className='login-wrapper'>
      <div className="back-img"></div>
      {currentView()}
    </div>
    {contextHolder}
  </section>
}

export default EntryPage
