import React, { useState, useEffect, useRef } from 'react';

const CanvasParticles = () => {
  const canvasRef = useRef(null);
  const [particles, setParticles] = useState([]);
  const [w, setW] = useState(window.innerWidth);
  const [h, setH] = useState(window.innerHeight);
  const tid = useRef(null);

  const opts = {
    particleColor: "rgb(200,200,200)",
    lineColor: "rgb(200,200,200)",
    particleAmount: 30,
    defaultSpeed: 0.3,
    variantSpeed: 0.3,
    defaultRadius: 2,
    variantRadius: 2,
    linkRadius: 200,
  };

  const checkDistance = (x1, y1, x2, y2) => {
    return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
  };

  const linkPoints = (point1, hubs) => {
    for (let i = 0; i < hubs.length; i++) {
      let distance = checkDistance(point1.x, point1.y, hubs[i].x, hubs[i].y);
      let opacity = 1 - distance / opts.linkRadius;
      if (opacity > 0) {
        drawArea.lineWidth = 0.5;
        drawArea.strokeStyle = `rgba(${rgb[0]}, ${rgb[1]}, ${rgb[2]}, ${opacity})`;
        drawArea.beginPath();
        drawArea.moveTo(point1.x, point1.y);
        drawArea.lineTo(hubs[i].x, hubs[i].y);
        drawArea.closePath();
        drawArea.stroke();
      }
    }
  };

  const Particle = function (xPos, yPos) {
    this.x = xPos || Math.random() * w;
    this.y = yPos || Math.random() * h;
    this.speed = opts.defaultSpeed + Math.random() * opts.variantSpeed;
    this.directionAngle = Math.floor(Math.random() * 360);
    this.color = opts.particleColor;
    this.radius = opts.defaultRadius + Math.random() * opts.variantRadius;
    this.vector = {
      x: Math.cos(this.directionAngle) * this.speed,
      y: Math.sin(this.directionAngle) * this.speed
    };
    this.update = function () {
      this.border();
      this.x += this.vector.x;
      this.y += this.vector.y;
    };
    this.border = function () {
      if (this.x >= w || this.x <= 0) {
        this.vector.x *= -1;
      }
      if (this.y >= h || this.y <= 0) {
        this.vector.y *= -1;
      }
      if (this.x > w) this.x = w;
      if (this.y > h) this.y = h;
      if (this.x < 0) this.x = 0;
      if (this.y < 0) this.y = 0;
    };
    this.draw = function () {
      drawArea.beginPath();
      drawArea.arc(this.x, this.y, this.radius, 0, Math.PI * 2);
      drawArea.closePath();
      drawArea.fillStyle = this.color;
      drawArea.fill();
    };
  };

  const setup = () => {
    setParticles([]);
    resizeReset();
    for (let i = 0; i < opts.particleAmount; i++) {
      particles.push(new Particle());
    }
  };

  const loop = () => {
    if (drawArea) {
      drawArea.clearRect(0, 0, w, h);
      particles.forEach((particle) => {
        particle.update();
        particle.draw();
      });
      particles.forEach((particle) => {
        linkPoints(particle, particles);
      });
    }
    requestAnimationFrame(loop);
  };

  const resizeReset = () => {
    setW(window.innerWidth);
    setH(window.innerHeight);
    if (canvasRef.current) {
      canvasRef.current.width = w;
      canvasRef.current.height = h;
    }
  };

  const deBouncer = () => {
    clearTimeout(tid.current);
    tid.current = setTimeout(() => {
      resizeReset();
    }, 200);
  };

  const rgb = opts.lineColor.match(/\d+/g);
  let drawArea = null;

  useEffect(() => {
    if (canvasRef.current) {
      drawArea = canvasRef.current.getContext("2d");
    }

    window.addEventListener('resize', deBouncer);
    resizeReset();
    setup();
    loop();

    return () => {
      window.removeEventListener('resize', deBouncer);
      clearTimeout(tid.current);
    };
  }, []);

  return (
    <div className='banner'>
      <canvas ref={canvasRef} id="canvas" />
      <div id="text">
        <h1>Everything Connects</h1>
      </div>
    </div>
  );
};

export default CanvasParticles;